import * as React from 'react';
import styles from './JobOpenings.module.scss';
import { IJobOpeningsProps } from './IJobOpeningsProps';
import { IJobOpeningsState } from './IJobOpeningsState';
import { escape } from '@microsoft/sp-lodash-subset';
import { Link } from "office-ui-fabric-react/lib/components/Link";
import Moment from 'react-moment';
import 'moment-timezone';
import { Loader } from '../../common/Loading';
const resumeupload: string = require('../assets/resumeupload.png');
import common from '../../common/common';
import { UrlQueryParameterCollection } from '@microsoft/sp-core-library';


export default class JobOpenings extends React.Component<IJobOpeningsProps, IJobOpeningsState> {
  public constructor(props: IJobOpeningsProps) {
    super(props);
    //Initialize the State
    this.state = {

      items: [
        {
          ID: 0,
          Title: "Currently no job opening is available.",
          JobDescription: "",
          Created: "",
          Location: [{ Label: '' }]
        }
      ],
      ReqID: 0,
      isLoading: true,
    };
  }
  public async componentDidMount() {
    await this.GetCurrentUserLocation();
  }
  public componentWillReceiveProps(nextProps): void {
    this.GetCurrentUserLocation();
  }
  //get user's Current location 
  public async GetCurrentUserLocation() {
    //# TODO change Department to serviceline
    const response = await this.props.graphClient.api('/me/department') //get current user's department
      .get((error: any, department: any, rawResponse?: any) => {
        this.props.graphClient.api('/me/city') //get current user's city
          .get((error: any, city: any, rawResponse?: any) => {
            this.GetJobOpeningRecord(department.value, city.value);
          });
      });
  }
  //get Job Opening Record's data
  public GetJobOpeningRecord(department, city) {
    var method = 'get items for Global Leadership Thought';
    let newCommonObj: common = new common();
    var query;
    if (this.props.serviceline == undefined)  //serviceline not selected query is fetch alerts acording to current user's sericeline and location
      query = "?$select=*,TaxCatchAll/Term&$filter=TaxCatchAll/Term eq '" + department + "' and TaxCatchAll/Term eq '" + city + "'&$expand=TaxCatchAll&$orderby=Modified desc&$top=5";
    else if (this.props.serviceline == "ALL SERVICELINE") //serviceline not selected query is fetch alerts acording to all sericeline and user's location
      query = "?$select=*,TaxCatchAll/Term&$filter=TaxCatchAll/Term eq '" + city + "'&$expand=TaxCatchAll&$orderby=Modified desc&$top=5";
    else//serviceline not selected query is fetch alerts acording to selected sericeline and user's location
      query = "?$select=*,TaxCatchAll/Term&$filter=TaxCatchAll/Term eq '" + this.props.serviceline + "' and TaxCatchAll/Term eq '" + city + "'&$expand=TaxCatchAll&$orderby=Modified desc&$top=5";

    newCommonObj.getDataFromListUsingGuid(this.props.siteUrl, this.props.listId, query, method).then(async (res) => {
      if (res.data.value != undefined && res.data.value != null) {

        var Code = res.data.value;
      }
      this.setState({
        items: Code,
        isLoading: false
      })

      if (res.data.value.length > 0) {
        //if service line is all then ordring First Your serviceline display after that other
        if (this.props.serviceline == "ALL SERVICELINE") {
          var dataFiltered = res.data.value.filter(
            data => data.ADDepartment[0].Label == department//Filter by your service line
          )
          if (dataFiltered.length > 0) {
            if (dataFiltered.length == res.data.value.length) {
              this.setState({ items: dataFiltered, isLoading: false });
            }
            else {
              var i = 0;
              for (i = 0; i < dataFiltered.length; i++) {
                const index = res.data.value.indexOf(dataFiltered[i]);
                if (index > -1) {
                  res.data.value.splice(index, 1);
                }
              }
              let orderservice = [];
              dataFiltered.forEach(myservice => {
                orderservice.push({
                  "Title": myservice.Title,
                  "JobDescription": myservice.JobDescription,
                  "Created": myservice.Created,
                  "Location": myservice.Location        
                });
              });
              res.data.value.forEach(otherservice => {
                orderservice.push({
                  "Title": otherservice.Title,
                  "JobDescription": otherservice.JobDescription,
                  "Created": otherservice.Created,
                  "Location": otherservice.Location 
                });
              });
              var item; item = orderservice;
              this.setState({ items: item, isLoading: false });
            }
          }
          else {
            this.setState({ items: res.data.value, isLoading: false });
          }
        }
        else { this.setState({ items: res.data.value, isLoading: false }); }
      }
      else {
        this.setState({
          items: [{
            "ID": 0,
            "Title": `${this.props.serviceline} 's Job Opening 's not in your location`,
            "JobDescription": "",
            "Created": "",
            "Location": [{ Label: '' }]

          }],
          isLoading: false,
          ReqID: 0
        });
      }
    }).catch(error => {
      console.log(error);
    });
  }
  public render(): React.ReactElement<IJobOpeningsProps> {
  
    let siteurl = this.props.siteUrl;
    return (
      <div className={styles.jobOpenings}>
        <div className={styles.container}>
          <div className={styles.header}>
            CAREERS-IJP
          </div>
          {this.state.items.map(function (item, key) {
            return (<div>
             
            <div className={styles.title}>
              <a href={`item.RespectivePageLink.Url`} target="_blank">{item.Title}</a>
                <Link
                href={`${siteurl}/SitePages/ResumeUpload.aspx?RID=${item.ID}`}
                   target="_blank"  className={styles.resumeupload} >
                  <img src={`${resumeupload}`} className={styles.resumeupload} /></Link>
              </div>
              <div className={styles.datedisplay}>
                {<Moment format="DD MMM">
                  {item.Created}</Moment>} - {item.Location[0].Label}           
              </div>
           
              <div className={styles.description}>
                {item.JobDescription}
              </div>
            </div>
            )
          })}
            <Link href={`${this.props.siteUrl}/SitePages/Search.aspx?q=RID`} target='_blank' className={styles.viewAll}>View more</Link>
            
        </div>
      </div>
    );
    
  }
}


